#scroll-to-top {
  width: 50px;
  height: 50px;
  opacity: 0;
  z-index: 2;
  background: none;
  border: none;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 0;
  transition: transform 1s ease-in-out, opacity 1s ease-in-out;
  display: flex;
  position: fixed;
  bottom: 50px;
  right: 20px;
  transform: translateY(30%);
}

@media screen and (min-width: 768px) {
  #scroll-to-top {
    width: 60px;
    height: 60px;
    right: 30px;
  }
}

@media screen and (min-width: 1200px) {
  #scroll-to-top {
    right: 50px;
  }
}

.arrow {
  width: 2vw;
  height: 2vw;
  border-bottom: 4px solid #f84119;
  border-right: 4px solid #ffc226;
  margin: -5px;
  animation: 2s infinite animate;
  display: block;
}

.arrow:nth-child(2) {
  animation-delay: -.2s;
}

.arrow:nth-child(3) {
  animation-delay: -.4s;
}

@keyframes animate {
  0% {
    opacity: 0;
    transform: rotate(-135deg)translate(-15px, -15px);
  }

  50% {
    opacity: 1;
  }

  100% {
    opacity: 0;
    transform: rotate(-135deg)translate(15px, 15px);
  }
}

